@import './theme';

@font-face {
    font-family: 'Aleo Free Font';
    src: url('../assets/fonts/aleo-regular-webfont.woff2') format('woff2'),
         url('../assets/fonts/aleo-regular-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

body {
    font-family: 'Aleo Free Font';
    font-size: 20px;
    background-color: var(--color-background-main);
}

@keyframes pop-in {
    0% {
      opacity: 0;
      transform: scale(0.95);
    }
    70% {
        transform: scale(1.01);
    }
    100% {
        opacity: 1;
        transform: scale(1);
    }
}

.page {
    position: relative;
    width: 21.0cm;
    height: 29.7cm;
    padding: var(--padding-page-vertical) var(--padding-page-horizontal);
    background-color: var(--color-background-page);
    box-shadow: 5px 5px 5px #555;
    margin: 50px auto;
    animation: 500ms ease-out pop-in;

    header {
        h1 {
            margin-top: 0;
        }
    }
}
