@import './theme.scss';

#language-select {
    position: absolute;
    top: var(--padding-page-vertical);
    right: var(--padding-page-horizontal);

    .language-icon {
        width: 25px;
        height: 25px;
        margin: 2px;
        cursor: pointer;
        opacity: 0.7;

        &:hover {
            transform: scale(1.1);
            opacity: 1;
        }

        &.active {
            opacity: 1;
        }
    }
}